<template>
    <div class="battle_main" ref="battle_main">
        <div class="select_my_team current"  ref="select_my_team" @click="SwitchTeam(0)">我的队伍</div>
        <div class="texture" ></div>
        <div class="select_enemy_team"  ref="select_enemy_team" @click="SwitchTeam(1)">敌方队伍</div>
        <div class="visual_container" ref="visual_container">
            <div class="team_container"  v-for="t,t_index in $store.battle.teams.list" :key="t.index">
                <div class="warrior_config" v-for="w,wIndex in t" :key="w.index">
                    <div class="warrior_box" ref="warrior_config" @click="WarriorConfig(w,t_index,wIndex)">
                        <WarriorElongatedCard :warriorData="w" cardStyle="3_1" :showUpArms="true" />
                    </div>
                    <div class="tactics_box" v-if="w.id != -1">
                        <div class="tactics_slot" v-for="t,t_id in w.tacticsArr" :key="t.index"
                        @click="TacticsConfig(w,t,t_id)">
                            <TacticsIcon :tacticsData="t"/>
                        </div>
                    </div>
                    <div class="artifact_config" v-if="w.id != -1"
                    @click="ArtifactsConfig(w)">
                        <ArtifactCard :artifactData="w.artifact" />
                        <div class="text" v-if="!w.artifact">未授予</div>
                    </div>
                    <!-- 位置调节按钮 -->
                    <div class="adjust_position_btn" ref="adjust_position_btn"  v-show="wIndex != 2 && w.id != -1 && t[wIndex + 1].id != -1" 
                    @touchstart="$refs.adjust_position_btn[t_index*3 + wIndex].classList.add('click')"
                    @touchend="$refs.adjust_position_btn[t_index*3 + wIndex].classList.remove('click')" 
                    @click="ExchangeWarriorPosition(t,wIndex)"></div>
                </div>
            </div>
            
        </div>
        <div class="settings_btn_group">
            <!-- 保存阵容 -->
            <div class="save_lineup_btn" ref="save_lineup_btn"
            @touchstart="$refs.save_lineup_btn.classList.add('click')"
            @touchend="$refs.save_lineup_btn.classList.remove('click')"
            @click="SaveTeamLineup()"
            >保存阵容</div>
            <!-- 清空队伍 -->
            <div class="clear_team_btn" ref="clear_team_btn"
            @touchstart="$refs.clear_team_btn.classList.add('click')"
            @touchend="$refs.clear_team_btn.classList.remove('click')"
            @click="ClearTeam()"
            >清空队伍</div>
        </div>
        

        <!-- 开始战斗按钮 -->
        <div class="begin_battle_btn" ref="begin_battle_btn" 
        @touchstart="$refs.begin_battle_btn.classList.add('click')"
        @touchend="$refs.begin_battle_btn.classList.remove('click')"
        @click="BeginFight()">开始</div>
        <!-- 战报按钮 -->
        <div class="report_btn" ref="report_btn"
        @touchstart="$refs.report_btn.classList.add('click')"
        @touchend="$refs.report_btn.classList.remove('click')"
        @click="ReportPanel()"
        >战报</div>
        <!-- 阵容按钮 -->
        <div class="camp_btn" ref="camp_btn"
        @touchstart="$refs.camp_btn.classList.add('click')"
        @touchend="$refs.camp_btn.classList.remove('click')"
        @click="OpenLineup()"
        >阵容</div>
       <TopNav title="模拟对局" />
       <router-view/>
    </div>
</template>

<script>
import WarriorElongatedCard from '@/components/style/WarriorElongatedCard.vue';
import TacticsIcon from '@/components/style/TacticsIcon.vue';
import TopNav from '@/components/container/TopNav.vue';
import ArtifactCard from '@/components/style/ArtifactCard.vue';
import Driver from '@/battle/js/driver';
import Save from '@/util/save';
import { BattleUtil } from '@/util/battle';
import Warrior from '@/util/warrior';
export default {
    name : 'BattleMain',
    components:{
        TopNav,
        WarriorElongatedCard,
        TacticsIcon,
        ArtifactCard
    },
    data(){
        return{
            curTeamIdx : 0,
        }
    },
    methods:{
        //清理当前队伍配置
        ClearTeam(){
            let emptyData = new Warrior();
            for(let i in this.$store.battle.teams.list[this.curTeamIdx]){
                this.$store.battle.teams.list[this.curTeamIdx][i].CopyData(emptyData);
            }
            this.$store.Alert('队伍数据清理成功');
        },
        //宝物配置
        ArtifactsConfig(warriorData){
            this.$store.buffer.curWarrior = warriorData;
            if(this.$store.buffer.curWarrior.artifact){
                this.$store.buffer.curArtifact = this.$store.buffer.curWarrior.artifact
                this.$router.push({
                    path : `${this.$route.path}/artifact_info`,
                })
            }
            else{
                this.$router.push({
                    path : `${this.$route.path}/artifact_list`,
                })
            }
        },
        //切换选中队伍
        SwitchTeam(idx){
            this.curTeamIdx = idx
            if(idx == 0){
                this.$refs.select_my_team.classList.add('current');
                this.$refs.select_enemy_team.classList.remove('current');
                this.$refs.visual_container.style.left = 0;
            }else{
                this.$refs.select_my_team.classList.remove('current');
                this.$refs.select_enemy_team.classList.add('current');
                this.$refs.visual_container.style.left = -100 + 'vw';
            }
        },
        //交换武将位置
        ExchangeWarriorPosition(t,wId){
            let curWarrior = new Warrior();
            curWarrior.CopyData(t[wId]);
            t[wId].CopyData(t[wId+1]);
            t[wId+1].CopyData(curWarrior);
        },
        //配置武将
        WarriorConfig(warriorData,t_id,w_id){
            this.$store.buffer.editTeamIdx = t_id
            this.$store.buffer.editTeamWarriorIdx = w_id
            if(warriorData.id != -1){
                this.$store.buffer.curWarrior = warriorData
                this.$router.push({
                    path : `${this.$route.path}/warrior_info`,
                    query : {
                        
                    }
                })
            }
            else{
                this.$router.push({
                    path : `${this.$route.path}/warrior_list`,
                    query : {
                        show_all : true,
                        load_warrior_model:'copy',
                        from:'battle'
                    }
                })
            }
            
        },
        //配置战法
        TacticsConfig(w,t,t_index){
            this.$store.buffer.curWarrior = w
            this.$store.buffer.curTactics = t
            this.$store.buffer.editTactIdx = t_index
            //调整战法详情界面的设置
            let model = "show"
            if(t_index != 0 && t.id == -1) model = "load"
            else if(t_index != 0 && t.id != -1) model = "unload"
            // 表示卸载战法
            if(t.id != -1){
                this.$router.push({
                    path : `${this.$route.path}/tactics_info`,
                    query : {
                        model : model,
                    }
                })
            }
            // 表示安装战法
            else if(t.id == -1){
                this.$router.push({
                    path : `${this.$route.path}/tactics_list`,
                })
            }
            
        },
        //保存队伍到阵容
        SaveTeamLineup(){
            for(let i in this.$store.battle.teams.list[this.curTeamIdx]){
                if(this.$store.battle.teams.list[this.curTeamIdx][i].id == -1){
                    this.$store.Alert('至少需要配置3个武将!');
                    return false;
                }
            }
            let that = this;
            let f = function(){
                let b = new BattleUtil(that.$store)
                let res = b.AddLineup(that.$store.battle.teams.list[that.curTeamIdx]);
                if(res) that.$store.Alert('队伍保存成功');
                // for(let i in that.$store.battle.teams.list[0]){
                //     let newWarrior = new Warrior();
                //     newWarrior.CopyData(that.$store.buffer.curTeam[i]);
                //     that.$store.battle.teams.list[0][i] = newWarrior;
                // }
                // that.$router.push({
                //     path : `/battle`,
                // })
            }
            let params = {
                msg : `确定将该队伍添加到阵容列表中？`,
                CallBack : f,
            }
            this.$store.Prompt(params)
            
            
            
            // let s = new Save(this.$store);
            // s.AddLineup(this.$store.battle.teams.list[this.curTeamIdx]);
        },
        //开始模拟对局
        BeginFight(){
            //检查
            for(let i in this.$store.battle.teams.list){
                for(let j in this.$store.battle.teams.list[i]){
                    if(this.$store.battle.teams.list[i][j].id == -1){
                        this.$store.Alert('请先配置好队伍！');
                        return false;
                    }
                }
            }
            let driver = new Driver(this.$store.battle);
            driver.Main();
            this.$router.push({
                path: `${this.$route.path}/battle_report`,
            })
            this.$store.dirty.reportHistory = true;
            // if(avail){
                
            // }
            // else{
            //     this.$bus.$emit('alert','武将没有配置完整');
            // }
        },
        //历史战报界面
        ReportPanel(){
            this.$router.push({
                path: `${this.$route.path}/report_history`,
            })
        },
        //打开保存的阵容界面
        OpenLineup(){
            this.$router.push({
                path: `${this.$route.path}/lineup`,
            })
        }
    },
    mounted(){
        this.$store.AutoStyle(this.$refs.battle_main)
    }
}
</script>

